body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.map {
    width: 100%;
    background: grey;
}

.panel {
    width: 600px;
}
.center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}

.container {
  margin: auto;
  background: transparent;
  padding: 65px 0;
  color: #001f5e;
  border: none;
}


.container input {
  display: block;
  border: 1px solid #ccc;
  border-radius: 5px;
  background: #fff;
  padding: 1em;
  outline: none;
  width: 100%;
  margin-bottom: 1.5em;
}

.container button {
  background: #001f5e;
  color: #ffffff;
  border: 0px;
  border-radius: 5px;
  padding: 1em;
  display: block;
  width: 100%;
  max-width: 250px;
  margin: 0 auto 1.5em auto;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
}

.container button:hover {
  background-color: #00afaa;
}

.container button:disabled {
  background-color: #d3d3d3;
}

.bg-green {
  background-color: #00afaa;
}

.navbar-light .navbar-brand,
.navbar-light .navbar-nav .nav-link {
  color: #ffffff;
}

a {
  color: #ffffff;
}

.navbar-light .navbar-nav .nav-link:hover {
  color: #001f5e;
}

.link {
  color: #001f5e;
  background-color: #00afaa;
  padding: 1em;
  border-radius: 5px;
}

.link:hover {
  color: #00afaa;
  background-color: #ffffff;
}

.landing {
  background-color: #001f5e;
  color: #ffffff;
}

img {
  width: 100%;
}

button.nav-link {
  text-align: left;
}

.sgnOut {
  background-color: #ffffff;
}

.sgnOut:hover {
  background-color: #001f5e;
  color: #ffffff;
}
